body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

header{
    height: 50px;
    border: 1px solid black;
    margin-bottom: 10px;
    display: flex;
    justify-content: space-between;
    align-content: center;
}

.titulo{
    font-size: 25px;
    padding: 10px;
    color: #fff;
}
.botonIniciar{
    position: absolute;
    left: 12px;
    top: 58px
}
.carta{
    width: 125px;
    height: 125px;
   
}
.oculta{
 width: 125px;
 height: 125px;
 background-color:orange;
}
.contenido{
    width: 125px;
    height: 125px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(3, 220, 224);
}
.ReactFlipCard{
    width: 100%;
    height: 100%;
}
.tablero{
    display: flex;
    flex-wrap: wrap;
    width: 700px;
    height: 600px;
    margin: 0 auto;
    justify-content: space-around;
}
body{
  background-color: black;
}

div:focus{
  outline: none !important;
}
